.footer {
  bottom: 0px;
  background-color: #222;
  color: #fff;
  padding: 0.5em;
  text-align: center;
  width: 100%;
  position: absolute;
}

.social_list {
  display: flex;
  justify-content: center;
  list-style: none;
}

.social_list li {
  margin: 0 1em;
}

.social_list li:hover {
  color: #ffbb33;
}

.social_list svg {
  font-size: 1.5em;
  cursor: pointer;
}

.copy_right {
  margin-top: 1em;
}

.copy_right span {
  font-weight: bold;
  color: #ffbb33;
}
