.steps {
  display: flex;
  gap: 4rem;
  justify-content: center;
  align-items: center;
  max-width: 400px;
  margin: 0 auto;
  position: relative;
  margin-bottom: 2rem;
}

.steps::after {
  content: "";
  width: 380px;
  border-bottom: 1px solid #ccc;
  position: absolute;
  top: 20px;
}

.step {
  text-align: center;
  background-color: #efefef;
  z-index: 1;
  width: 120px;
  padding: 0.5rem;
}

.step svg {
  font-size: 1.9rem;
  margin-right: 0.2rem;
  fill: #9cc4e4;
}

.active {
  font-weight: bold;
}

.active svg {
  fill: #3a89c9;
}
